"use client";

import React, { useEffect, useCallback } from "react";
import * as fonts from "@/libs/fonts";
import useEmblaCarousel from "embla-carousel-react";
import Image from "next/image";

interface dataCarousel {
  id: number;
  title: string;
  description: string;
  icon: any;
  bgColor: string;
}

type PropType = {
  slides: dataCarousel[];
};

const EmblaCarousel: React.FC<PropType> = (props) => {
  const { slides } = props;
  
  // Important: Set loop to false to prevent slide duplication
  const [emblaRef, emblaApi] = useEmblaCarousel({
    align: 'start',
    containScroll: 'trimSnaps',
    dragFree: true,
    loop: false // Explicitly set to false to prevent duplication
  });

  // Make sure the carousel is properly initialized only once
  useEffect(() => {
    if (emblaApi) {
      // Initialize the carousel
      emblaApi.reInit();
    }
    
    // Cleanup function
    return () => {
      // Clean up if component unmounts
    };
  }, [emblaApi]);

  return (
    <div className="embla w-full">
      <div className="embla__viewport" ref={emblaRef}>
        <div className="embla__container flex flex-nowrap">
          {slides.map((slide) => (
            <div
              className="embla__slide w-[21vw] max-md:w-[40vw] max-sm:w-[80vw] h-auto p-[22px] rounded-[20px] mr-[2vw]"
              style={{
                border: `2px solid ${slide.bgColor}`,
                color: "rgba(69, 26, 26, 1)",
              }}
              key={slide.id}
            >
              <div className="embla__slide__content">
                <div className="container relative text-start flex flex-col">
                  <div className="container mb-4">
                    {/* Circular container with responsive size */}
                    <div
                      className="rounded-full aspect-square w-full max-w-[106px] relative flex items-center justify-center"
                      style={{
                        background: `${slide.bgColor}`,
                        border: "6px solid rgba(255, 255, 255, 1)",
                      }}
                    >
                      {/* Icon with responsive positioning */}
                      <div className="relative w-3/5 h-3/5 flex items-center justify-center">
                        <Image
                          src={slide.icon}
                          alt={`${slide.title} icon`}
                          fill
                          className="object-contain p-1"
                        />
                      </div>
                    </div>
                  </div>
                  <div className="container relative">
                    <h5
                      className={`${fonts.space_grotesk} text-[22px] font-[700] leading-[24px] align-middle mb-4`}
                    >
                      {slide.title}
                    </h5>
                    <p
                      className={`${fonts.funnel_sans} text-[14px] font-[300] leading-[22px] align-middle`}
                    >
                      {slide.description}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default EmblaCarousel;